import { useRef, useState } from "react";
import { Link } from "react-router-dom";

const Card = (props) => {
  const answerElRef = useRef();
  const [state, setState] = useState(false);
  const [answerH, setAnswerH] = useState("0px");
  const { lecture, idx, closeSide } = props;

  const handleOpenAnswer = () => {
    setState(!state);
    setAnswerH(`min-content`);
  };

  return (
    <div
      className="space-y-3 overflow-hidden mt-5 border-b"
      key={idx}
      onClick={handleOpenAnswer}
    >
      <h4 className="cursor-pointer pb-1 flex items-center justify-between text-md text-gray-700 font-medium">
        <div className="flex flex-row justify-between w-full">
          <div className="text-right w-1/2">
            <p> {lecture.name}</p>
          </div>
        </div>
        {state ? (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-5 w-5 text-gray-500 ml-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              stroke-width="2"
              d="M20 12H4"
            />
          </svg>
        ) : (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-5 w-5 text-gray-500 ml-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 4v16m8-8H4"
            />
          </svg>
        )}
      </h4>
      <div
        ref={answerElRef}
        className="duration-300"
        style={state ? { height: answerH } : { height: "0px" }}
      >
        <div className="mt-3">
          {lecture.lectures.map((item, id) => (
            <button
              onClick={() => closeSide(idx, id)}
              className="text-gray-500 w-full"
            >
              <p className="inline-flex items-center justify-start w-full m-2 text-color-blue">
                <img
                  alt="icon"
                  className="ml-4"
                  src="data:image/png;base64,"
                />{" "}
                {item.title}
              </p>
              <hr />
            </button>
          ))}
              {lecture.quiz && (
            <Link to={`/quiz/${idx}`} className="text-gray-500">
              <p className="inline-flex items-center justify-start w-full m-2 text-color-blue">
                <img
                  alt=""
                  className="ml-4"
                  src="data:image/png;base64,"
                />{" "}
                {lecture.quiz.name}
              </p>
              <hr />
            </Link>
          )}
        </div>
      </div>
    </div>
  );
};

const LecturList = ({ closeSide }) => {
  const sessions = window.config?.main["sessions"];

  return (
    <section className="leading-relaxed max-w-screen-xl mt-12 mx-auto bg-white p-5 rounded ">
      <div className="space-y-3 text-right rounded">
        <p className="text-xl text-gray-800">   </p>
      </div>
      <div className="mt-4 mx-auto">
        {sessions.map((item, idx) => (
          <Card idx={idx} lecture={item} closeSide={closeSide} />
        ))}
      </div>
    </section>
  );
};
export default LecturList;
